import React, { useEffect, useState } from 'react';
import { ChakraProvider, Box } from '@chakra-ui/react';
import { Routes, Route, Navigate } from 'react-router-dom';
import { supabase } from './services/supabase';
import theme from './theme';
import adminTheme from './theme/adminTheme';
import userTheme from './theme/userTheme';
import Layout from './components/Layout';
import AdminLayout from './components/AdminLayout';
import ProtectedRoute from './components/ProtectedRoute';

// User pages
import Home from './pages/Home';
import BookPage from './pages/Book';
import CategoryBooks from './pages/CategoryBooks';

// Admin pages
import AdminLogin from './pages/Admin/Login';
import SignUp from './pages/Admin/SignUp';
import Dashboard from './pages/Admin/Dashboard';
import ContentManager from './pages/Admin/ContentManager';
import Analytics from './pages/Admin/Analytics';
import BookManager from './pages/Admin/BookManager';
import NoMatch from './pages/Admin/NoMatch';

// Components
import Navbar from './components/Navbar';
import Login from './pages/Login';
import UserProfile from './pages/UserProfile';

// Admin Route Component
const AdminRoute = ({ children }) => {
  const [session, setSession] = useState(null);
  const [loading, setLoading] = useState(true);
  const [isAdmin, setIsAdmin] = useState(false);

  useEffect(() => {
    const checkAdminStatus = async () => {
      try {
        const { data: { session } } = await supabase.auth.getSession();
        setSession(session);

        if (session?.user?.id) {
          // Check user role from profiles table
          const { data, error } = await supabase
            .from('profiles')
            .select('role')
            .eq('id', session.user.id)
            .single();

          if (error) {
            console.error('Error checking admin status:', error);
            setIsAdmin(false);
          } else {
            setIsAdmin(data.role === 'admin');
          }
        }

        setLoading(false);
      } catch (error) {
        console.error('Error in checkAdminStatus:', error);
        setLoading(false);
        setIsAdmin(false);
      }
    };

    checkAdminStatus();

    // Listen for auth changes
    const { data: { subscription } } = supabase.auth.onAuthStateChange(async (event, session) => {
      setSession(session);
      
      if (session?.user?.id) {
        const { data, error } = await supabase
          .from('profiles')
          .select('role')
          .eq('id', session.user.id)
          .single();

        if (error) {
          console.error('Error checking admin status:', error);
          setIsAdmin(false);
        } else {
          setIsAdmin(data.role === 'admin');
        }
      } else {
        setIsAdmin(false);
      }
    });

    return () => subscription.unsubscribe();
  }, []);

  if (loading) {
    return null;
  }

  if (!session || !isAdmin) {
    return <Navigate to="/admin/login" replace />;
  }

  return children;
};

const App = () => {
  // Check if the current path is admin-related
  const isAdminRoute = window.location.pathname.startsWith('/admin');

  return (
    <ChakraProvider theme={isAdminRoute ? adminTheme : userTheme}>
      <Box minH="100vh">
        <Navbar />
        <Routes>
          {/* User Routes */}
          <Route element={<Layout />}>
            <Route path="/" element={<Home />} />
            <Route path="book/:bookId" element={<BookPage />} />
            <Route path="category/:categoryId" element={<CategoryBooks />} />
            <Route path="/login" element={<Login />} />
            <Route
              path="/profile"
              element={
                <ProtectedRoute>
                  <UserProfile />
                </ProtectedRoute>
              }
            />
          </Route>

          {/* Admin Routes - Separate URL structure */}
          <Route path="/admin">
            {/* Public Admin Routes */}
            <Route path="login" element={<AdminLogin />} />
            <Route path="signup" element={<SignUp />} />

            {/* Protected Admin Routes */}
            <Route element={
              <AdminRoute>
                <AdminLayout />
              </AdminRoute>
            }>
              <Route index element={<Dashboard />} />
              <Route path="books" element={<BookManager />} />
              <Route path="content" element={<ContentManager />} />
              <Route path="analytics" element={<Analytics />} />
            </Route>
          </Route>

          {/* Error Handling */}
          <Route path="/404" element={<NoMatch />} />
          <Route path="*" element={<Navigate to="/404" replace />} />
        </Routes>
      </Box>
    </ChakraProvider>
  );
};

export default App;