// src/pages/CategoryBooks/index.jsx
import React, { useEffect, useState } from 'react';
import { useParams, Link } from 'react-router-dom';
import { 
  Box, 
  Container, 
  Heading, 
  SimpleGrid, 
  Spinner, 
  Alert, 
  AlertIcon,
  Text,
  Breadcrumb,
  BreadcrumbItem,
  BreadcrumbLink,
  useColorModeValue,
  Badge,
  Button,
  Input,
  InputGroup,
  InputLeftElement,
  VStack,
  HStack,
  Icon,
  Fade,
  ScaleFade,
  useBreakpointValue,
  Tooltip,
  Divider,
  useDisclosure,
  Collapse,
  useToast
} from '@chakra-ui/react';
import { motion, AnimatePresence } from 'framer-motion';
import { supabase } from '../../services/supabase';
import { toggleBookmark } from '../../services/supabase';
import BookCard from '../../components/BookCard';
import { FiSearch, FiBook, FiFilter, FiX, FiChevronDown, FiChevronUp, FiHome } from 'react-icons/fi';
import { useAuth } from '../../hooks/useAuth';

const MotionBox = motion(Box);
const MotionContainer = motion(Container);

const CategoryBooks = () => {
  const { categoryId } = useParams();
  const [category, setCategory] = useState(null);
  const [books, setBooks] = useState([]);
  const [filteredBooks, setFilteredBooks] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [searchQuery, setSearchQuery] = useState('');
  const [bookmarks, setBookmarks] = useState(new Set());
  const { user } = useAuth();
  const toast = useToast();
  
  // Color mode values
  const bgGradient = useColorModeValue(
    'linear(to-br, blue.50, purple.50)',
    'linear(to-br, gray.800, gray.900)'
  );
  const searchBg = useColorModeValue('white', 'gray.700');
  const cardBg = useColorModeValue('white', 'gray.700');
  const textColor = useColorModeValue('gray.600', 'gray.300');
  const highlightColor = useColorModeValue('blue.500', 'blue.300');
  const headerBg = useColorModeValue('rgba(255, 255, 255, 0.8)', 'rgba(25, 25, 25, 0.8)');
  const borderColor = useColorModeValue('gray.200', 'gray.700');
  const mutedTextColor = useColorModeValue('gray.500', 'gray.400');

  // Responsive values
  const containerPadding = useBreakpointValue({ base: 4, md: 8 });
  const gridColumns = useBreakpointValue({ base: 1, md: 2, lg: 3 });

  useEffect(() => {
    const fetchCategoryData = async () => {
      try {
        setLoading(true);
        setError(null);
        
        console.log('Fetching category data for ID:', categoryId);
        
        // Fetch category details
        const { data: categoryData, error: categoryError } = await supabase
          .from('Categories')
          .select('*')
          .eq('id', categoryId)
          .single();
        
        if (categoryError) throw categoryError;
        
        // Fetch books for this category with complete information
        const { data: booksData, error: booksError } = await supabase
          .from('Books')
          .select(`
            id, 
            title, 
            cover_image, 
            display_order, 
            status,
            avg_rating,
            category_id,
            Categories (
              name
            )
          `)
          .eq('category_id', categoryId)
          .eq('status', true)
          .order('display_order', { ascending: true });
        
        if (booksError) throw booksError;
        
        console.log('Category data:', categoryData);
        console.log('Books data:', booksData);
        
        setCategory(categoryData);
        setBooks(booksData || []);
        setFilteredBooks(booksData || []);
        
        // Fetch user bookmarks if logged in
        if (user?.id) {
          const { data: bookmarksData, error: bookmarksError } = await supabase
            .from('Bookmarks')
            .select('book_id')
            .eq('user_id', user.id);
            
          if (bookmarksError) {
            console.error('Error fetching bookmarks:', bookmarksError);
          } else if (bookmarksData) {
            const bookmarkIds = new Set(bookmarksData.map(b => b.book_id));
            setBookmarks(bookmarkIds);
          }
        }
      } catch (err) {
        console.error('Error fetching category data:', err);
        setError(err.message || 'Failed to load category data');
        toast({
          title: 'Error',
          description: `Failed to load category data: ${err.message}`,
          status: 'error',
          duration: 5000,
          isClosable: true,
        });
      } finally {
        setLoading(false);
      }
    };

    if (categoryId) {
      fetchCategoryData();
    }
  }, [categoryId, user?.id, toast]);

  // Handle search
  useEffect(() => {
    if (searchQuery.trim() === '') {
      setFilteredBooks(books);
    } else {
      const filtered = books.filter(book =>
        book.title.toLowerCase().includes(searchQuery.toLowerCase())
      );
      setFilteredBooks(filtered);
    }
  }, [searchQuery, books]);

  const handleBookmarkToggle = async (bookId) => {
    try {
      if (!user) {
        toast({
          title: 'Authentication required',
          description: 'Please sign in to bookmark books',
          status: 'warning',
          duration: 3000,
          isClosable: true,
        });
        return;
      }

      // Use the toggleBookmark helper function instead of RPC
      const { data, error } = await toggleBookmark(bookId, user.id);
      
      if (error) throw error;

      // Update local bookmarks state
      setBookmarks(prev => {
        const newBookmarks = new Set(prev);
        if (newBookmarks.has(bookId)) {
          newBookmarks.delete(bookId);
          // Show success notification for removing bookmark
          toast({
            title: 'Bookmark removed',
            description: 'Book has been removed from your bookmarks',
            status: 'success',
            duration: 2000,
            isClosable: true,
            position: 'top-right',
          });
        } else {
          newBookmarks.add(bookId);
          // Show success notification for adding bookmark
          toast({
            title: 'Book bookmarked!',
            description: 'Book has been added to your bookmarks',
            status: 'success',
            duration: 2000,
            isClosable: true,
            position: 'top-right',
          });
        }
        return newBookmarks;
      });
    } catch (err) {
      console.error('Error toggling bookmark:', err);
      toast({
        title: 'Error',
        description: err.message || 'Failed to update bookmark',
        status: 'error',
        duration: 3000,
        isClosable: true,
      });
    }
  };

  if (loading) {
    return (
      <Box display="flex" justifyContent="center" minH="100vh" alignItems="center" bgGradient={bgGradient}>
        <VStack spacing={4}>
          <Spinner 
            size="xl" 
            thickness="4px" 
            color={highlightColor}
            emptyColor="gray.200"
            speed="0.65s"
          />
          <Text color={textColor} fontSize="lg">Loading books...</Text>
        </VStack>
      </Box>
    );
  }

  if (error) {
    return (
      <Container maxW="container.xl" py={8} bgGradient={bgGradient} minH="100vh">
        <ScaleFade initialScale={0.9} in={true}>
          <Alert 
            status="error" 
            variant="left-accent" 
            borderRadius="xl" 
            my={6}
            flexDirection="column"
            alignItems="center"
            textAlign="center"
            p={8}
            bg={cardBg}
            shadow="xl"
          >
            <AlertIcon boxSize="32px" mr={0} mb={4} />
            <Text fontSize="xl" mb={4} fontWeight="medium">
              Error loading books: {error}
            </Text>
            <Button 
              as={Link} 
              to="/" 
              colorScheme="blue" 
              size="lg"
              leftIcon={<FiBook />}
            >
              Return to Home
            </Button>
          </Alert>
        </ScaleFade>
      </Container>
    );
  }

  return (
    <Box 
      minH="100vh" 
      bgGradient={bgGradient}
      py={containerPadding}
      transition="all 0.2s"
    >
      <MotionContainer
        maxW="container.xl"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
      >
        <VStack spacing={8} width="100%">
          {/* Breadcrumb Navigation */}
          <Breadcrumb separator=">" width="100%" color={mutedTextColor} mb={2}>
            <BreadcrumbItem>
              <BreadcrumbLink as={Link} to="/" display="flex" alignItems="center">
                <Icon as={FiHome} mr={1} />
                Home
              </BreadcrumbLink>
            </BreadcrumbItem>
            <BreadcrumbItem isCurrentPage>
              <BreadcrumbLink>{category?.name || 'Category'}</BreadcrumbLink>
            </BreadcrumbItem>
          </Breadcrumb>
          
          {/* Category Header with Search */}
          <VStack spacing={6} width="100%" align="center" pb={6}>
            <Heading 
              as="h1" 
              size="2xl" 
              textAlign="center"
              bgGradient={`linear(to-r, ${highlightColor}, purple.500)`}
              bgClip="text"
              letterSpacing="tight"
            >
              {category?.name}
            </Heading>
            
            <Badge 
              colorScheme="blue" 
              variant="subtle" 
              fontSize="md"
              px={4}
              py={1}
              borderRadius="full"
            >
              {filteredBooks.length} {filteredBooks.length === 1 ? 'Book' : 'Books'}
            </Badge>

            {/* Search Bar */}
            <Box width="100%" maxW="600px" mx="auto" mt={4}>
              <InputGroup size="lg">
                <InputLeftElement pointerEvents="none" h="full" pl={4}>
                  <Icon as={FiSearch} color={mutedTextColor} boxSize="1.5em" />
                </InputLeftElement>
                <Input
                  placeholder="Search books in this category..."
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  bg={searchBg}
                  borderRadius="full"
                  borderWidth="2px"
                  borderColor={searchQuery ? highlightColor : borderColor}
                  _hover={{ borderColor: highlightColor }}
                  _focus={{ borderColor: highlightColor, boxShadow: 'outline' }}
                  transition="all 0.2s"
                  fontSize="lg"
                  pl={12}
                  pr={searchQuery ? 16 : 6}
                  h={14}
                />
                {searchQuery && (
                  <Button
                    position="absolute"
                    right="3"
                    top="50%"
                    transform="translateY(-50%)"
                    size="md"
                    colorScheme="brand"
                    variant="ghost"
                    onClick={() => setSearchQuery('')}
                    zIndex={2}
                    h={10}
                    w={10}
                    borderRadius="full"
                  >
                    <Icon as={FiX} boxSize="1.2em" />
                  </Button>
                )}
              </InputGroup>
            </Box>

            <Divider borderColor={borderColor} mt={6} />
          </VStack>

          {/* Books Grid */}
          <AnimatePresence>
            <SimpleGrid 
              columns={gridColumns}
              spacing={8}
              width="100%"
            >
              {filteredBooks.map((book, index) => (
                <MotionBox
                  key={book.id}
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  exit={{ opacity: 0, y: -20 }}
                  transition={{ duration: 0.3, delay: index * 0.1 }}
                  whileHover={{ y: -4 }}
                >
                  <BookCard 
                    book={book}
                    isBookmarked={bookmarks.has(book.id)}
                    onBookmarkToggle={() => handleBookmarkToggle(book.id)}
                    categoryName={category?.name}
                  />
                </MotionBox>
              ))}
            </SimpleGrid>
          </AnimatePresence>

          {filteredBooks.length === 0 && (
            <ScaleFade initialScale={0.9} in={true}>
              <Box 
                textAlign="center" 
                p={12} 
                borderRadius="2xl" 
                bg={cardBg}
                boxShadow="xl"
                maxW="600px"
                mx="auto"
              >
                <Icon as={FiBook} fontSize="4xl" color={textColor} mb={4} />
                <Text fontSize="xl" color={textColor} mb={6}>
                  {searchQuery 
                    ? "No books found matching your search" 
                    : "No books available in this category yet"}
                </Text>
                <Button 
                  as={Link} 
                  to="/" 
                  colorScheme="blue" 
                  size="lg"
                  leftIcon={<FiBook />}
                >
                  Browse Other Categories
                </Button>
              </Box>
            </ScaleFade>
          )}
        </VStack>
      </MotionContainer>
    </Box>
  );
};

export default CategoryBooks;