import React, { useEffect, useState } from 'react';
import {
  Box,
  Flex,
  Button,
  useColorModeValue,
  Stack,
  useColorMode,
  Container,
  IconButton,
  Menu,
  MenuButton,
  MenuList,
  MenuItem,
  MenuDivider,
  Avatar,
  Text,
  HStack,
} from '@chakra-ui/react';
import { Link as RouterLink, useLocation } from 'react-router-dom';
import { FiSun, FiMoon, FiUser, FiLogOut, FiBookmark, FiSettings } from 'react-icons/fi';
import { supabase } from '../services/supabase';

const Navbar = () => {
  const { colorMode, toggleColorMode } = useColorMode();
  const [session, setSession] = useState(null);
  const [userRole, setUserRole] = useState(null);
  const location = useLocation();
  
  const bg = useColorModeValue('white', 'gray.800');
  const borderColor = useColorModeValue('gray.200', 'gray.700');

  const isAdminRoute = location.pathname.startsWith('/admin');

  useEffect(() => {
    // Get initial session
    supabase.auth.getSession().then(async ({ data: { session } }) => {
      setSession(session);
      if (session?.user?.id) {
        // Fetch user role from profiles
        const { data, error } = await supabase
          .from('profiles')
          .select('role')
          .eq('id', session.user.id)
          .single();
        
        if (!error && data) {
          setUserRole(data.role);
        }
      }
    });

    // Listen for auth changes
    const { data: { subscription } } = supabase.auth.onAuthStateChange(async (_event, session) => {
      setSession(session);
      if (session?.user?.id) {
        const { data, error } = await supabase
          .from('profiles')
          .select('role')
          .eq('id', session.user.id)
          .single();
        
        if (!error && data) {
          setUserRole(data.role);
        }
      } else {
        setUserRole(null);
      }
    });

    return () => subscription.unsubscribe();
  }, []);

  const handleSignOut = async () => {
    try {
      const { error } = await supabase.auth.signOut();
      if (error) throw error;
      window.location.href = userRole === 'admin' ? '/admin/login' : '/login';
    } catch (error) {
      console.error('Error signing out:', error);
    }
  };

  // Don't show navbar on login pages
  if (location.pathname === '/login' || location.pathname === '/admin/login') {
    return null;
  }

  // Show admin navbar for admin routes
  if (isAdminRoute) {
    if (userRole !== 'admin') {
      return null;
    }

    return (
      <Box
        position="sticky"
        top={0}
        zIndex="sticky"
        bg={bg}
        borderBottom="1px"
        borderColor={borderColor}
      >
        <Container maxW="container.xl">
          <Flex h={16} alignItems="center" justifyContent="space-between">
            <Button
              as={RouterLink}
              to="/admin"
              variant="ghost"
              fontSize="xl"
              fontWeight="bold"
              _hover={{ textDecoration: 'none' }}
            >
              BookShorts Admin
            </Button>

            <Stack direction="row" spacing={4} alignItems="center">
              <IconButton
                icon={colorMode === 'light' ? <FiMoon /> : <FiSun />}
                onClick={toggleColorMode}
                variant="ghost"
                aria-label="Toggle color mode"
              />

              <Menu>
                <MenuButton>
                  <Avatar
                    size="sm"
                    name={session?.user?.email}
                  />
                </MenuButton>
                <MenuList>
                  <MenuItem icon={<FiUser />}>
                    Admin Profile
                  </MenuItem>
                  <MenuItem as={RouterLink} to="/admin/books" icon={<FiSettings />}>
                    Manage Books
                  </MenuItem>
                  <MenuDivider />
                  <MenuItem onClick={handleSignOut} icon={<FiLogOut />} color="red.400">
                    Sign Out
                  </MenuItem>
                </MenuList>
              </Menu>
            </Stack>
          </Flex>
        </Container>
      </Box>
    );
  }

  // Show user navbar for non-admin routes
  return (
    <Box
      position="sticky"
      top={0}
      zIndex="sticky"
      bg={bg}
      borderBottom="1px"
      borderColor={borderColor}
    >
      <Container maxW="container.xl">
        <Flex h={16} alignItems="center" justifyContent="space-between">
          <Button
            as={RouterLink}
            to="/"
            variant="ghost"
            fontSize="xl"
            fontWeight="bold"
            _hover={{ textDecoration: 'none' }}
          >
            BookShorts
          </Button>

          <Stack direction="row" spacing={4} alignItems="center">
            <IconButton
              icon={colorMode === 'light' ? <FiMoon /> : <FiSun />}
              onClick={toggleColorMode}
              variant="ghost"
              aria-label="Toggle color mode"
            />

            {session ? (
              <Menu>
                <MenuButton>
                  <Avatar
                    size="sm"
                    name={session.user.email}
                    src={session.user.user_metadata?.avatar_url}
                  />
                </MenuButton>
                <MenuList>
                  <MenuItem as={RouterLink} to="/profile" icon={<FiUser />}>
                    Profile
                  </MenuItem>
                  <MenuItem as={RouterLink} to="/bookmarks" icon={<FiBookmark />}>
                    Bookmarks
                  </MenuItem>
                  <MenuDivider />
                  <MenuItem onClick={handleSignOut} icon={<FiLogOut />} color="red.400">
                    Sign Out
                  </MenuItem>
                </MenuList>
              </Menu>
            ) : (
              <Button
                as={RouterLink}
                to="/login"
                colorScheme="brand"
              >
                Sign In
              </Button>
            )}
          </Stack>
        </Flex>
      </Container>
    </Box>
  );
};

export default Navbar; 