import React, { useEffect, useState } from 'react';
import { useParams, useLocation } from 'react-router-dom';
import { 
  Box, 
  Container, 
  Heading, 
  Image, 
  Link, 
  Spinner, 
  Alert, 
  AlertIcon,
  HStack, 
  Text, 
  Progress,
  IconButton,
  Flex,
  useColorModeValue,
  useBreakpointValue,
  Badge,
  Button,
  VStack,
  Icon,
  useToast,
  Stat,
  StatLabel,
  StatNumber,
  StatGroup,
  Divider
} from '@chakra-ui/react';
import { motion, AnimatePresence } from 'framer-motion';
import { Link as RouterLink } from 'react-router-dom';
import { FiArrowLeft, FiBookmark, FiShare2, FiClock, FiStar, FiEye } from 'react-icons/fi';
import useBookData from './useBookData';
import ContentSection from './ContentSection';
import { supabase } from '../../services/supabase';
import { incrementBookClicks } from '../../services/supabase';

const MotionBox = motion(Box);
const MotionContent = motion(Box);

const BookPage = () => {
  const { bookId } = useParams();
  const query = new URLSearchParams(useLocation().search);
  const section = query.get('section') || 'core';
  const { book, content, loading, error } = useBookData(bookId, section);
  const [readingProgress, setReadingProgress] = React.useState(0);
  const [bookData, setBookData] = useState(null);
  const toast = useToast();
  
  // Move all color mode hooks to top level to ensure consistent order
  const coverSize = useBreakpointValue({ base: '150px', md: '250px' });
  const bgGradient = useColorModeValue(
    'linear(to-r, blue.50 0%, purple.50 100%)',
    'linear(to-r, gray.800 0%, gray.900 100%)'
  );
  const textColor = useColorModeValue('gray.600', 'gray.300');
  const bgColor = useColorModeValue('white', 'gray.800');
  const borderColor = useColorModeValue('gray.200', 'gray.700');

  const handleProgressUpdate = () => {
    setReadingProgress(prev => (prev >= 100 ? 0 : prev + 25));
  };

  useEffect(() => {
    const fetchBook = async () => {
      try {
        const { data, error } = await supabase
          .from('Books')
          .select(`
            *,
            Categories (
              id,
              name
            )
          `)
          .eq('id', bookId)
          .single();

        if (error) throw error;

        setBookData(data);
        
        // Increment click count
        await incrementBookClicks(bookId);
      } catch (error) {
        console.error('Error fetching book:', error);
        toast({
          title: 'Error fetching book',
          description: error.message,
          status: 'error',
          duration: 3000,
          isClosable: true,
        });
      }
    };

    fetchBook();
  }, [bookId]);

  if (loading) {
    return (
      <Box display="flex" justifyContent="center" alignItems="center" minH="100vh">
        <Spinner size="xl" thickness="3px" color="blue.500" />
      </Box>
    );
  }

  if (error) {
    return (
      <Alert status="error" variant="left-accent" borderRadius="md" my={6}>
        <AlertIcon />
        Error loading book: {error.message}
      </Alert>
    );
  }

  if (!bookData) {
    return (
      <Container maxW="container.lg" py={8}>
        <Text>Book not found</Text>
      </Container>
    );
  }

  return (
    <MotionBox
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
      minH="100vh"
      bgGradient={bgGradient}
    >
      <Container maxW="container.xl" py={8}>
        <Flex mb={6} align="center" justify="space-between">
          <Link as={RouterLink} to="/" display="flex" alignItems="center">
            <FiArrowLeft />
            <Text ml={2}>Back to Home</Text>
          </Link>
          <HStack>
            <IconButton
              aria-label="Bookmark"
              icon={<FiBookmark />}
              variant="ghost"
            />
            <IconButton
              aria-label="Share"
              icon={<FiShare2 />}
              variant="ghost"
            />
          </HStack>
        </Flex>

        <MotionBox
          initial={{ y: 20, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          transition={{ duration: 0.4 }}
        >
          <Flex direction={{ base: 'column', md: 'row' }} align="center" mb={8}>
            <Image
              src={bookData.cover_image || '/placeholder-book.jpg'}
              alt={bookData.title}
              boxSize={coverSize}
              objectFit="cover"
              borderRadius="lg"
              shadow="xl"
              mr={{ md: 8 }}
              mb={{ base: 4, md: 0 }}
            />
            <Box textAlign={{ base: 'center', md: 'left' }}>
              <Heading as="h1" size="2xl" mb={2}>{bookData.title}</Heading>
              <Badge colorScheme="purple" fontSize="lg" mb={2}>
                {bookData.Categories?.name}
              </Badge>
              <HStack spacing={4} justify={{ base: 'center', md: 'flex-start' }}>
                <Text fontSize="lg" color={textColor}>
                  <FiClock style={{ marginRight: '4px', display: 'inline' }} />
                  {section === 'core' ? '15 min read' : '30 min read'}
                </Text>
                <Badge colorScheme={bookData.status ? 'green' : 'red'}>
                  {bookData.status ? 'Published' : 'Draft'}
                </Badge>
              </HStack>
              <HStack spacing={4} justify={{ base: 'center', md: 'flex-start' }}>
                <Icon as={FiStar} color="yellow.400" />
                <Text>{bookData.avg_rating?.toFixed(1) || '0.0'}</Text>
                <Text>({bookData.total_ratings || 0} ratings)</Text>
              </HStack>
              <HStack spacing={4} justify={{ base: 'center', md: 'flex-start' }}>
                <Icon as={FiEye} />
                <Text>{bookData.click_count || 0} views</Text>
              </HStack>
            </Box>
          </Flex>
        </MotionBox>

        <Box mb={8}>
          <Text fontWeight="500" mb={2}>Reading Progress</Text>
          <Progress 
            value={readingProgress} 
            size="sm" 
            colorScheme="blue" 
            borderRadius="full"
          />
        </Box>

        <HStack spacing={4} mb={8} justify="center">
          <Link
            as={RouterLink}
            to={`/book/${bookId}?section=core`}
            p={4}
            borderRadius="md"
            fontWeight="semibold"
            bg={section === 'core' ? 'blue.500' : 'transparent'}
            color={section === 'core' ? 'white' : 'gray.500'}
            _hover={{
              textDecoration: 'none',
              bg: section === 'core' ? 'blue.600' : 'gray.100'
            }}
            transition="all 0.2s"
          >
            Core Explanation
            <Badge ml={2} colorScheme="blue">
              Summary
            </Badge>
          </Link>
          <Link
            as={RouterLink}
            to={`/book/${bookId}?section=deep_dive`}
            p={4}
            borderRadius="md"
            fontWeight="semibold"
            bg={section === 'deep_dive' ? 'purple.500' : 'transparent'}
            color={section === 'deep_dive' ? 'white' : 'gray.500'}
            _hover={{
              textDecoration: 'none',
              bg: section === 'deep_dive' ? 'purple.600' : 'gray.100'
            }}
            transition="all 0.2s"
          >
            Deep Dive
            <Badge ml={2} colorScheme="purple">
              Detailed
            </Badge>
          </Link>
        </HStack>

        <AnimatePresence mode="wait">
          <MotionContent
            key={section}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
            transition={{ duration: 0.3 }}
          >
            {content ? (
              <ContentSection 
                content={content.content}
                lastUpdated={content.last_updated ? new Date(content.last_updated) : new Date()}
              />
            ) : (
              <Text textAlign="center" color="gray.500">
                Content not available for this section
              </Text>
            )}
          </MotionContent>
        </AnimatePresence>

        <Box mt={12} textAlign="center">
          <Text fontSize="lg" mb={4}>
            {readingProgress}% Completed
          </Text>
          <Button 
            colorScheme="blue" 
            size="lg"
            onClick={handleProgressUpdate}
            leftIcon={<FiBookmark />}
          >
            Mark as {readingProgress >= 100 ? 'Restart' : 'Completed'}
          </Button>
        </Box>

        <Box p={8}>
          <VStack spacing={6} align="stretch">
            <StatGroup>
              <Stat>
                <StatLabel>Average Rating</StatLabel>
                <StatNumber>
                  <HStack spacing={2}>
                    <Icon as={FiStar} color="yellow.400" />
                    <Text>{bookData.avg_rating?.toFixed(1) || '0.0'}</Text>
                  </HStack>
                </StatNumber>
              </Stat>
              <Stat>
                <StatLabel>Total Ratings</StatLabel>
                <StatNumber>{bookData.total_ratings || 0}</StatNumber>
              </Stat>
              <Stat>
                <StatLabel>Views</StatLabel>
                <StatNumber>{bookData.click_count || 0}</StatNumber>
              </Stat>
            </StatGroup>

            <Divider />

            <VStack align="start" spacing={4}>
              <Heading size="md">About this book</Heading>
              <Text>{bookData.description || 'No description available.'}</Text>
            </VStack>
          </VStack>
        </Box>
      </Container>
    </MotionBox>
  );
};

export default BookPage;