// src/hooks/useAuth.js
import { useEffect, useState } from 'react';
import { supabase } from '../services/supabase';

export const useAuth = () => {
  const [state, setState] = useState({
    user: null,
    loading: true,
    error: null
  });

  useEffect(() => {
    let isMounted = true;
    let subscription;

    const handleAuthChange = async (event, session) => {
      if (!isMounted) return;
      
      try {
        // For SIGNED_IN and TOKEN_REFRESHED events
        if (session?.user) {
          console.log('Auth event:', event, 'User:', session.user.email);
          setState({ user: session.user, loading: false, error: null });
        } else {
          // For SIGNED_OUT events, verify with getSession
          const { data: { session: currentSession } } = await supabase.auth.getSession();
          setState({
            user: currentSession?.user || null,
            loading: false,
            error: currentSession ? null : 'No active session'
          });
        }
      } catch (error) {
        setState({ user: null, loading: false, error: error.message });
      }
    };

    const initializeAuth = async () => {
      try {
        // Initial session check
        const { data: { session }, error } = await supabase.auth.getSession();
        
        if (!isMounted) return;
        
        if (error) throw error;
        
        // Set initial state
        setState({
          user: session?.user || null,
          loading: false,
          error: null
        });

        // Setup listener after initial check
        const { data: { subscription: sub } } = supabase.auth.onAuthStateChange(handleAuthChange);
        subscription = sub;

      } catch (error) {
        setState({ user: null, loading: false, error: error.message });
      }
    };

    initializeAuth();

    return () => {
      isMounted = false;
      subscription?.unsubscribe();
    };
  }, []);

  const signOut = async () => {
    try {
      setState(prev => ({ ...prev, loading: true }));
      const { error } = await supabase.auth.signOut();
      if (error) throw error;
      
      // Force full state reset
      setState({ user: null, loading: false, error: null });
      
      // Optional: Redirect after successful sign out
      window.location.href = '/';
    } catch (error) {
      setState(prev => ({ ...prev, loading: false, error: error.message }));
      throw error;
    }
  };

  return {
    ...state,
    isAuthenticated: !!state.user,
    signOut
  };
};